.App {
  text-align: center;
}

* {
  font-family: "Montserrat", sans-serif;
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

.navbar__header {
  height: 75px;
  width: 100%;
  background-color: #24252a;
  color: #edf0f1;
  font-size: 24px;
  font-weight: 700;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.navbar__header__right__div .fragment {
  display: flex;
  justify-content: space-around;
  text-decoration: none;
  color: #edf0f1;

  padding: 10px;
}

.navbar__header__right__div .link {
  display: flex;
  justify-content: space-around;
  text-decoration: none;
  font-weight: 600;
  color: turquoise;
  border: 1px solid turquoise;
  padding: 5px;
  margin-left: 5px;
  font-size: 15px;
  transition: 0.4s ease-in-out;
}
.navbar__header__right__div .link:hover {
  background: #edf0f1;
  border: 1px solid #edf0f1;
  color: #24252a;
}
.navbar__header__bottom__div {
  color: #24252a;
  background: turquoise;
  height: 75px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-transform: uppercase;
}

.navbar__header__bottom__div__links {
  text-decoration: none;
  color: #24252a;
  font-weight: 500;
  font-size: 24px;
}

.login__div {
  width: 300px;
  position: relative;
  top: 150px;
  left: 40%;
}
.login__div__form {
  width: 300px;
}

.login__div__form div input[type="text"],
input[type="password"] {
  width: 250px;
  margin-bottom: 20px;
  border: 2px solid turquoise;
  padding: 10px;
}
.login__div__form div input[type="submit"] {
  width: 100px;
  height: 30px;
  color: turquoise;
  border: 1px solid turquoise;
  margin-bottom: 15px;
}

.login__div__h2,
.login__div__p {
  padding: 15px 0px;
}

.home__div {
}
.home__div__top__section {
  display: flex;
  justify-content: center;
}

.home__div__top__section__form {
  width: 100%;
  padding: 25px;
  display: flex;
  justify-content: space-between;
  background: blue;
  box-sizing: border-box;
  margin-top: 15px;
}

.home__div__top__section__form__div {
  display: grid;
  grid-template-columns: repeat(5, 1fr);

  width: 90%;
}
.home__div__top__section__form button {
  border: 2px solid crimson;
  position: relative;
  top: 48px;
  color: crimson;
  padding: 10px;
  font-size: 16px;
  height: 40px;
  transition: 0.4s ease-in-out;
}
.home__div__top__section__form button:hover {
  color: #ffff;
  background: crimson;
}
.home__div__top__section__form__div__div {
  display: grid;
  grid-template-columns: 1fr;
  grid-column-gap: 1em;
  justify-items: center;
  padding: 12px;
}

.home__div__top__section__form__div__div label {
  color: #ffffff;
  font-weight: 500;
  font-size: 18px;
  width: 150px;
  margin-bottom: 15px;
}
.home__div__top__section__form__div__div select,
input[type="text"] {
  width: 150px;
  height: 40px;
  border: 2px solid crimson;
  font-size: 16px;
  background: #ffff;
  color: crimson;
}
.home__div__middle__section {
  border: 4px solid royalblue;

  height: 100%;
}

.home__div__bottom__section {
  border: 1px solid crimson;
  height: 50px;
}

.card__before__show {
  border: 1px solid pink;
  width: 100%;
  display: flex;

  justify-content: space-around;
}
.card {
  border: 1px solid green;
  background-color: #d3d3d3;
}

.card__title {
  font-size: 12px;
  color: #24252a;
}
.card__location {
  color: crimson;
  font-size: 12px;
}
.card__info {
  color: blue;
}
